import picture_bg from '../../assets/picture_bg.jpg'

export default {
	
  components: {
  	headerV : header,
  	footerV : footer
  },
  data () {
    return {
    	banner : [banner],
    	picture_bg : "background:url('"+ picture_bg +"') center center no-repeat",
    	superiority_head : [
    		{
    			title : '服务优势',
