    	],
    	superiority_con : [
    		{
    			src : picture_07,
    			title : '支持多终端跨屏展示',
    			content : '人靠衣装马靠鞍，一个炫酷的网站才配得上创业者的新潮创意。本公司采用术，拥有大气舒适的界面和众多精美模板',
